@import url(https://fonts.googleapis.com/css?family=Literata:600|Montserrat:500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#content {
  text-align: center;
  width: calc(100vw - (100vw - 100%));
  min-height: 100vh;
  font-family: Literata, sans-serif;
  line-height: 1.3;
  color: #ffffffbf;
  background: #36454f;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: 1fr 1fr 1fr 1fr auto; }

.row {
  padding: 30px;
  -webkit-align-items: center;
          align-items: center;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-auto-rows: minmax(100px, auto);
  grid-gap: 10px;
  grid-template-areas: 'header details details details details'; }
  .row:nth-child(odd) {
    background: #1f1b24; }
  .row header {
    padding-right: 30px;
    font-size: 2em;
    grid-area: header;
    font-weight: 600; }
  .row:first-of-type header {
    -webkit-transform: rotate(-7deg);
            transform: rotate(-7deg); }
  .row .details {
    text-align: left;
    font-family: Montserrat, sans-serif;
    grid-area: details;
    font-weight: 500; }
    .row .details span {
      color: #ffa600e6; }
    .row .details .footnote {
      font-size: 0.85em;
      font-style: italic; }
    .row .details .contact {
      color: #ffa600e6;
      text-decoration: none;
      border-bottom: 1.5px solid #ffa600e6;
      cursor: pointer;
      display: inline-block;
      transition: all .25s; }
      .row .details .contact:hover {
        -webkit-filter: brightness(1.2);
                filter: brightness(1.2); }

.footer {
  text-align: right;
  font-size: 0.75em;
  font-family: Montserrat, sans-serif;
  color: #ffffffbb;
  background: black; }
  .footer p {
    margin: 4px 8px; }

@media screen and (max-width: 760px) {
  #content {
    grid-template-rows: auto; }
  .row {
    padding: 16px;
    grid-template-columns: repeat(1, 1fr);
    grid-template-areas: 'header' 'details'; }
    .row header {
      padding-right: 0;
      text-align: center; } }

